* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  max-width: 100vw;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: unset;
}
ul,
li {
  list-style: none;
}
.header__wrapper {
  height: 190px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0px 70px;
}

p {
  margin-bottom: 0;
}

.seccion1 {
  height: 120px;
  background-color: #fff;
  position: relative;
}
.patron__header {
  height: 20px;
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 0;
  background-image: url("./../assets/img/Patron__header.png");
  background-size: 35%;
  animation-name: deslizando;
  animation-duration: 50s;
  animation-iteration-count: infinite;
  animation-fill-mode: none;
}
@keyframes deslizando {
  0% {
    background-position: 0px 0px;
  }
  100% {
    background-position: 2000px 0px;
  }
}
.seccion2 {
  height: 40px;
  background-color: rgb(141, 177, 184, 0.3);
}
.header__main,
.header__list {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}
.btn__menu {
  outline: none;
  color: #666666;
  background-color: transparent;
  font-size: 28px;
  border: none;
  padding-bottom: 8px;
}
.buscarmq {
  width: 0px;
  outline: none;
  border: none;
  margin: 0 5px 0 5px;
  border-bottom: 1px solid rgb(221, 85, 89);
  transition: width 0.5s ease-in;
}
.btn__mq2,
.buscadormq {
  display: none;
}
.header__logo,
.header__buscador,
.header__links {
  display: flex;
  min-width: 250px;
  justify-content: center;
}
.header__logo {
  background-color: #fff;
}
.imagen__logo {
  max-width: 250px;
}
.header__links,
.lih,
.header__navlink {
  display: flex;
  flex-direction: row;
  gap: 10px;
  align-items: center;
  text-decoration: none;
  color: #666666;
  font-size: 16px;
}
.link__Carrito {
  padding-left: 15px;
}
.fas {
  border: none;
  font-size: 25px;
  background-color: transparent;
  color: rgb(11, 76, 96);
}
.fa-search {
  color: rgb(221, 85, 89);
  padding-bottom: 5px;
}
.buscar {
  border: none;
  padding-left: 20px;
  padding-bottom: 5px;
}
.buscar:focus {
  outline: none;
}
::placeholder {
  color: rgb(166, 172, 166);
  border: none;
}
.buscador {
  width: 230px;
  border-bottom: 1px solid rgb(221, 85, 89);
}
.header__list {
  justify-content: center;
  gap: 60px;
}
.header__navlink {
  font-size: 14px;
  transition: font-size 0.3s;
}
.header__navlink:hover {
  font-size: 16px;
}
.seccion3 {
  height: 30px;
  background-color: rgb(221, 85, 89);
  color: #fff;
  text-align: center;
}

.main {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  position: relative;
}
.aside__bar {
  width: 0px;
  background-color: #fff;
  position: absolute;
  left: 0;
  top: 50px;
  z-index: 1000;
  box-shadow: 2px 2px 5px rgb(34, 34, 34, 0.5);
  transition: width 0.5s ease-in;
}
.sidebar__list {
  display: flex;
  flex-direction: column;
  padding-left: 0;
  margin-bottom: 0;
}
.sidebar__ite {
  height: 40px;
}
.sidebar__link {
  display: block;
  position: relative;
  height: 100%;
  width: 200px;
  text-decoration: none;
  font-size: 0px;
  transition: font-size 0.5s ease-in-out;
  cursor: inherit;
}
.sidebar__link > span {
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 0%;
  transition: 0.5s ease-in-out;
  -webkit-transition: 0.5s ease-in-out;
}
.sidebar__link:after {
  display: flex;
  padding-left: 30px;
  align-items: center;
  content: attr(data-title);
  height: 100%;
  color: rgb(11, 76, 96);
}
.sidebar__link > span > span {
  display: none;
  height: 40px;
  background: rgb(11, 76, 96, 0.5);
}
.sidebar__link:hover > span {
  width: 100%;
}
/*--------*/
.box {
  width: 100%;
  height: auto;
  overflow: hidden;
}
.box img {
  width: 100vw;
  height: auto;
}
@supports (object-fit: cover) {
  .box img {
    object-fit: cover;
    object-position: center center;
  }
}
.galeria__wrapper {
  width: 100%;
  margin: 20px 60px;
}
.divs__info {
  min-height: 130px;
  display: flex;
  gap: 30px;
  margin: 30px 0 30px 0;
}
.divs__info1,
.divs__info2,
.divs__info3,
.divs__info4 {
  flex: 1;
  background-color: rgb(237 237 237);
  border-radius: 10px;
  display: flex;
  flex-direction: row;
  padding: 5px;
  justify-content: space-evenly;
  align-content: center;
  gap: 10px;
}
.i__divsinfo {
  min-width: 50px;
  height: 50px;
  background-color: #fff;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  align-self: center;
}
.i__divsinfo > .fas {
  font-size: 30px;
  color: rgb(246 88 92);
}
.text__divsinfo {
  display: flex;
  flex-direction: column;
  max-width: 180px;
  gap: 10px;
  align-self: center;
}
.sub__divsinfo {
  font-size: 14px;
  color: rgb(19 87 107);
  font-weight: 600;
}
.info__divsinfo {
  font-size: 12px;
  color: rgb(132 132 132);
}
.padre1 {
  min-height: 200px;
  display: flex;
  gap: 30px;
}
.p1Hijo1,
.p1Hijo2,
.p1Hijo3,
.p1Hijo4 {
  flex: 1;
  background-color: #cbd6eb;
  border-radius: 10px;
}
.padre2 {
  min-height: 250px;
  display: flex;
  gap: 30px;
  margin-top: 20px;
}
.p2Hijo1,
.p2Hijo2 {
  flex: 1;
  background-color: #cbd6eb;
  border-radius: 10px;
}
.padre3 {
  min-height: 400px;
  display: flex;
  gap: 30px;
  margin-top: 20px;
}
.padrastro {
  display: flex;
  min-height: auto;
  flex: 1;
  gap: 30px;
}
.p3Hijo1,
.p3Hijo2,
.p3Hijo3 {
  flex: 1;
  background-color: #cbd6eb;
  border-radius: 10px;
}
.padre4 {
  min-height: 200px;
  display: flex;
  gap: 30px;
  margin-top: 20px;
}
.p4Hijo1,
.p4Hijo2,
.p4Hijo3,
.p4Hijo4 {
  flex: 1;
  background-color: #cbd6eb;
  border-radius: 10px;
}
.padre5 {
  min-height: 250px;
  display: flex;
  margin: 20px 0;
  border-radius: 10px;
  background-color: #cbd6eb;
  flex: 1;
}

/*---------------------------------------------------------------------------*/

.navbar2 {
  width: 100%;
  background-color: #f65559;
  position: relative;
  z-index: 10000;
}

.navbar2__wrapper {
  margin: auto;
  max-width: 1040px;
  display: flex;
  justify-content: space-between;
  height: 100px;
  padding: 10px 10px;
  color: #eee;
}

.navbar2__item {
  width: 80px;
  text-align: center;
}

.categoria__section {
  max-width: 1260px;
  margin: 0 auto;
  padding: 20px 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.titulo__section {
  max-width: 100%;
  display: flex;
  gap: 30px;
  font-size: 35px;
  font-weight: bold;
  letter-spacing: 2px;
  color: rgb(102, 102, 102);
}

img {
  max-width: 100%;
}

.carousel {
  max-width: 100%;
  margin: 30px 0;
  display: flex;
  gap: 20px;
  position: relative;
}

.carousel-nav-left,
.carousel-nav-rigth {
  background-repeat: no-repeat;
  position: absolute;
  top: calc(50% - 45px);
  width: 40px;
  height: 45px;
  cursor: pointer;
}

.carousel-nav-left {
  background-image: url(../assets/iconos/back.svg);
  left: -50px;
  z-index: 1000;
}

.carousel-nav-rigth {
  background-image: url(../assets/iconos/next.svg);
  right: -50px;
  z-index: 1000;
}

.card2 {
  padding: 5px;
  width: calc(100% / 4);
  background-color: white;
  border: 1px solid #eee;
  position: relative;
  cursor: pointer;
}

.card__descuento {
  background-color: red;
  color: white;
  width: 50px;
  height: 20px;
  font-size: 13px;
  position: absolute;
  top: 20px;
  left: 0;
  text-align: end;
  padding: 2px 4px;
}

.card__img {
  max-width: 100%;
}

.card__title {
  font-size: 18px;
}

.card__precio {
  margin: 20px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  font-size: 20px;
  font-weight: 400;
  color: #222;
}

.precio__descuento {
  color: red;
}

a {
  text-decoration: none;
}

.btn__verMas {
  background-color: #004c60;
  color: #eee;
  padding: 7px 55px;
  font-size: 18px;
}

section.marcas {
  padding-bottom: 0;
}

ul.carousel {
  margin: unset;
}

.carousel-container {
  position: relative;
}

.envio__container {
  display: flex;
  gap: 20px;
  padding: 20px 100px;
}

.envio__info {
  width: calc(100% / 4);
}

.container__suscribete {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
  color: #888;
}

.suscribete {
  font-size: 30px;
}

.input__container {
  border: 1px solid #ddd;
  padding: 5px;
  width: 300px;
  height: 50px;
  display: flex;
}

.input__container > button {
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.input__suscribete {
  border: none;
  width: calc(100% - 10px);
  outline: none;
}

