footer {
  width: 100%;
  background-color: #f1f1f1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.EL__footer {
  padding-top: 40px;
  width: 900px;
  height: 330px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 35px;
}
.footer__container {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  gap: 10px;
}

.footer__container.left {
  width: 50%;
  gap: 100px;
}
.leftfooter {
  display: flex;
  justify-content: flex-start;
}
.footer_container.left > ul {
  padding: 0 10px;
  width: 50%;
  display: flex;
}

footer strong {
  color: #004c60;
  font-size: 18px;
  font-weight: 600;
}

footer a {
  color: #222;
  font-size: 14px;
  line-height: 18px;
}
.footer__logo img {
  width: 210px;
}
.footer__logo {
  padding-left: 220px;
}
.a1p,
.a2p {
  display: flex;
  gap: 55px;
}

.a1p > ul,
.a2p > ul {
  width: 50%;
}

@media (max-width: 900px) {
  .header__wrapper {
    height: 50px;
    padding: 0px 10px;
  }
  .patron__header {
    display: none;
  }
  .imagen__logo {
    max-height: 40px;
    padding-top: 4px;
  }
  .header__buscador,
  .seccion2,
  .seccion3,
  .mc {
    display: none;
  }
  .header__logo,
  .header__links,
  .btn__mq2 {
    min-width: 150px;
  }
  .fas {
    font-size: 20px;
  }
  .car {
    color: #fff;
    font-size: 13px;
  }
  .car__div {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #666666;
    border-radius: 50%;
    width: 18px;
    height: 18px;
    margin-bottom: 20px;
  }
  .aside__bar.open {
    width: 200px;
  }
  .aside__bar.open .sidebar__link {
    font-size: 15px;
    cursor: pointer;
  }
  .aside__bar.open .sidebar__link > span > span {
    display: block;
  }
  .btn__mq2,
  .buscadormq,
  .buscarmq {
    display: unset;
  }
  .fa-search {
    padding-left: 15px;
    color: #666666;
  }
  .btn__mq2 {
    display: flex;
    align-items: center;
    gap: 10px;
    max-width: 180px;
  }
  .buscarmq.open-mobile {
    width: 95px;
  }
  /*---------*/
  .galeria__wrapper {
    margin: 0 50px;
  }
  .divs__info {
    min-height: 280px;
  }
  .padre2 {
    flex-direction: column;
  }
  .p1Hijo1,
  .p1Hijo2,
  .p1Hijo3,
  .p1Hijo4 {
    min-height: 200px;
  }
  .padre2 {
    min-height: 500px;
  }
  .padre3 {
    min-height: 250px;
  }
  .p4Hijo1,
  .p4Hijo2,
  .p4Hijo3,
  .p4Hijo4 {
    min-height: 200px;
  }
  .padre5 {
    min-height: 150px;
  }
  .padre1,
  .padre4,
  .divs__info {
    flex-direction: column;
  }
  .EL__footer {
    width: 480px;
    height: 420px;
    gap: 10px;
    padding-left: 10px;
  }
  .rightfooter,
  .leftfooter {
    flex-direction: column;
  }

  .footer__container.left {
    width: 100%;
    justify-content: flex-start;
    gap: 155px;
  }
  .footer__logo {
    padding-left: 130px;
    padding-top: 30px;
  }
}
@media (max-width: 480px) {
  .header__logo,
  .header__links,
  .btn__mq2 {
    min-width: 100px;
  }
  /*----------*/
  .galeria__wrapper {
    margin: 0 20px;
  }
  .divs__info {
    min-height: 200px;
  }
  .p1Hijo1,
  .p1Hijo2,
  .p1Hijo3,
  .p1Hijo4 {
    min-height: 150px;
  }
  .padre2 {
    min-height: 280px;
  }
  .padre3 {
    min-height: 150px;
  }
  .p4Hijo1,
  .p4Hijo2,
  .p4Hijo3,
  .p4Hijo4 {
    min-height: 150px;
  }
  .padre5 {
    min-height: 130px;
  }
}
/*  --------------------------------  */

@media (max-width: 1260px) {
  .card {
    width: calc(100% / 4);
  }
  .envio__info {
    width: calc((100% - 200px) / 2);
  }
  .envio__info > img {
    width: 100%;
  }
}

@media (max-width: 978px) {
  .navbar2__item {
    width: 40px;
    font-size: 12px;
  }
  .card {
    width: calc(100% / 4);
  }
  .envio__info {
    width: calc((100% - 20px) / 2);
  }
  .card__precio {
    flex-direction: column;
  }
  footer {
    padding: 35px;
  }
  footer strong {
    font-size: 14px;
  }
  footer a {
    font-size: 12px;
  }
}

@media (max-width: 780px) {
  section {
    padding: 20px;
  }
  .card {
    width: calc(100% / 3);
  }
  .card:nth-child(3) {
    display: none;
  }
  .carousel-nav-left {
    left: -10px;
  }
  .carousel-nav-rigth {
    right: -10px;
  }
}
